.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  position: fixed;
  height: 120px;
  z-index: 100000;
  background-color: black;
}
.logo {
  height: 120px;
}
.app {
  margin-top: 150px;
}
